import _ from 'lodash';
import META_DEFAULT from '../app/editor/constant/default-meta';
import { MAP_APP_KEY } from '../constant';
import Vue from 'vue';
import VueResource from 'vue-resource';
Vue.use(VueResource);


const C = {
  LibImg: 'LibImg',
  Page: 'Page',
  Meta: 'Meta',
};
const LibImg = AV.Object.extend(C.LibImg); // {owner,image} 素材图片
const Page = AV.Object.extend(C.Page); // {data} 渲染数据
const Meta = AV.Object.extend(C.Meta); // {} 描述信息
function ownIt(obj) {
  obj.set('owner', AV.User.current());
}

/*
 Meta 是一个单页的描述信息，不包含渲染信息
 Page 就是纯粹的渲染信息，不关注其他内容
 Meta <-- Page

 保存
 1. 新建
 创建 Meta 根据 Meta.id 作为单页的唯一标识，Page 保存 mid(Meta.id) 作为外键

 2. 他人作品
 跟新建一样，但是要传入原来的 mid 作为模板

 3. 打开自己作品
 根据 url 中的 id 查询 Meta 和 Page
 */

export function getUserPicList() {
  return new Promise((resolve) => {
    const query = new AV.Query('LibImg');
    query.equalTo('owner', AV.User.current());
    query.descending('createdAt');
    query.find().then((libImgs) => {
      const urls = libImgs.map(libImg => libImg.get('image').url());
      resolve(urls);
    }).catch((error) => {
      console.log(JSON.stringify(error));
    });
  });
}

export function removePic(url) {
  const query = new AV.Query('LibImg');
  query.equalTo('owner', AV.User.current());
  query.find().then((libImgs) => {
    libImgs.some((libImg) => {
      if (libImg.get('image').url() === url) {
        console.log('find a libimg', url)
        libImg.destroy().then(
          () => {
            console.log('删除成功', url);
          },
          () => {
            console.log('删除失败', url);
          });
        return true;
      }
      return false;
    });
  }).catch((error) => {
    console.log(JSON.stringify(error));
  });
}

export function uploadLibImg({ name, base64, done }) {
// { loaded: 1234, total: 2468, percent: 50 }
  const avFile = new AV.File(name, { base64 });
  const libImg = new LibImg();
  libImg.set('owner', AV.User.current());
  libImg.set('image', avFile);
  libImg.save().then(
    () => {
      // 文件保存成功
      if (done) {
        console.log('保存图片成功')
        done(libImg.get('image').url());
      }
    },
    (error) => {
      // 异常处理
      console.error(JSON.stringify(error));
    });
}


export function createPage(mid, canvasData) {
  return new Promise((resolve, reject) => {
    const page = new Page();
    page.set('data', canvasData);
    page.set('mid', mid);
    page.save().then(() => {
      console.log('新增 page 成功', page.toJSON());
      resolve(page.id);
    }, () => {
      console.log('新增 page 失败');
      reject();
    });
  });
}


function genMeta(obj) {
  const meta = new Meta();
  _.forOwn(_.merge({}, META_DEFAULT, obj), (v, k) => {
    meta.set(k, v);
  });
  ownIt(meta);
  return meta;
}

export function createMeta(obj) {
  return new Promise((resolve, reject) => {
    genMeta(obj).save().then((meta) => {
      console.log('新增 meta 成功', meta.toJSON());
      createPage(meta.id).then(() => {
        resolve(meta.toJSON());
      }, reject);
    }, () => {
      console.log('新增 meta 失败');
      reject();
    });
  });
}

export function folkMeta(folkId, obj) {
  return new Promise((resolve, reject) => {
    const meta = AV.Object.createWithoutData(C.Meta, folkId);
    meta.fetch().then(() => {
      console.log(`query ${folkId} success`);
      const newMetaObj = _.merge({}, meta.attributes, obj);
      delete newMetaObj.owner;
      createMeta(newMetaObj).then(resolve, reject);
    }, () => {
      console.log(`query ${folkId} fail`);
      reject();
    });
  });
}

export function updateMeta(metaId, obj) {
  return new Promise((resolve, reject) => {
    const meta = AV.Object.createWithoutData(C.Meta, metaId);
    _.forOwn(obj, (v, k)=> {
      meta.set(k, v);
    });
    meta.save().then(() => {
      console.log('保存 meta 成功', meta.toJSON());
      resolve(meta.toJSON());
    }, () => {
      console.log('保存 meta 失败');
      reject();
    });
  });
}

export function queryMeta(metaId) {
  return new Promise((resolve, reject) => {
    const meta = AV.Object.createWithoutData(C.Meta, metaId);
    meta.fetch().then(() => {
      console.log(`query ${metaId} success`);
      resolve(meta.toJSON(), meta);
    }, () => {
      console.log(`query ${metaId} fail`);
      reject();
    });
  });
}


export function queryPageByMeta(metaId) {
  return new Promise((resolve, reject) => {
    const query = new AV.Query(C.Page);
    query.equalTo('mid', metaId);
    query.first().then((page) => {
      console.log(`query page by meta ${metaId} success`);
      resolve(page.toJSON());
    }, () => {
      console.log(`query page by meta ${metaId} fail`);
      reject();
    });
  });
}

export function savePageByMetaId(mid, canvasData) {
  return new Promise((resolve, reject) => {
    const query = new AV.Query(C.Page);
    query.equalTo('mid', mid);
    query.first().then((page) => {
      page.set('data', canvasData);
      page.save().then(() => {
        console.log('保存 page 成功', page.toJSON());
        resolve();
      }, () => {
        console.log('保存 page 失败');
        reject();
      });
    }, () => {
      reject();
    });
  });
}

export function getMetaList() {
  return new Promise((resolve, reject) => {
    const query = new AV.Query(C.Meta);
    query.equalTo('owner', AV.User.current());
    query.descending('createdAt');
    query.find().then((metas) => {
      resolve(metas.map(meta => meta.toJSON()));
    }, reject);
  });
}

export function delMeta(metaId) {
  const meta = AV.Object.createWithoutData(C.Meta, metaId);
  meta.destroy().then(() => {
    console.log('删除 meta 成功', metaId);
  }, () => {
    console.log('删除 meta 失败', metaId);
  });
}

export function searchAddressByKeyword(kw) {
  return new Promise((resolve, reject) => {
    const url = 'http://apis.map.qq.com/ws/place/v1/suggestion/';
    Vue.http.jsonp(url, {
      params: {
        keyword: kw,
        key: MAP_APP_KEY,
        output: 'jsonp',
      },
    }).then((resp) => {
      resolve(resp.data && resp.data.data || []);
    }, reject);
  });
}



