function getUserAccount() {
  const strAccount = window.localStorage.getItem('__account');
  return strAccount ? window.JSON.parse(strAccount) : null;
}

function setUserAccount(account) {
  window.localStorage.setItem('__account', window.JSON.stringify(account));
}

function genUsername() {
  const strTime = window.Date.now() + '';
  const arr = [];
  for (let i = 0; i < strTime.length; i++) {
    const digit = parseInt(strTime.charAt(i), 10);
    arr.push('abcdefghijk'.charAt(digit));
  }
  return arr.join('');
}

export default function autologin() {
  AV.init({
    appId: 'MOJCrJUm89QJ6BBpeQobclrq-gzGzoHsz',
    appKey: '87C5oGvYyBRPmkHWamSVjiHy',
  });

  if (!AV.User.current()) {
    let account = getUserAccount();
    if (!account) {
      const user = new AV.User();
      account = {
        username: genUsername(),
        password: '123456',
      };
      user.setUsername(account.username);
      user.setPassword(account.password);
      user.signUp().then(() => {
        console.log('注册成功');
        setUserAccount(account);
      }, ((error) => {
        console.log('自动注册失败', JSON.stringify(error));
      }));
    }
    AV.User.logIn(account.username, account.password).then(() => {
      console.log('登录成功');
    }, (error) => {
      console.log('自动登录失败', JSON.stringify(error));
    });
  } else {
    console.log('处于登录状态');
  }
}




